.exhibition-wrap {
    background-color: #EFF2F7;
    padding-bottom: 4rem;
    .section-list {
        .exhibition-view {
            background-color: #fff;
            border-radius: 1.5rem;
            padding: 0 0 2.3rem;
            h2 {
                @include Bold;
                @include Align-middle-left;
                padding: 0 1.7rem 0 3.4rem;
                height: 6rem;
                font-size:1.4rem;
                @include Background($sprite:'bg-pick', $size:7.5rem auto, $position:top left 11rem, $path:'bgs');
            }
    
            .exhibition-view-list {
                .slick-slide {
                    display: inline-block;
                    width:28rem !important;
                    vertical-align: top;
                    text-align: left;
                    margin-left: -2rem;
                    margin-right: 3rem;
                    a {
                        .img {
                            position: relative;
                            .cover {
                                border-radius: 1rem;
                                overflow: hidden;
                                background-color: #fff;
                            }
                        }
                        .text {
                            margin-top: 2.4rem;
                            padding: 0 1rem;
                            strong {
                                display: block;
                                @include Ellipsis($lines:2);
                                @include Bold;
                                line-height: 1.3;
                                word-break: keep-all;
                                font-size:1.7rem;
                            }
                            span {
                                display: block;
                                color: #999;
                                margin-top: 1rem;
                            }
                        }
                    }
                }
    
            }
    
    
        }
    
        .exhibition-list {
            padding:2rem 1.7rem 0;
            h3 {
                @include Bold;
                font-size: 1.2rem;
                b {color:#F04040;}
            }
        }
    }

    .section-detail { 
        .exhibition-list-wrap {
            h3 {
                @include Bold;
                font-size:1.5rem;
                padding: 3rem 1.7rem 2.1rem;
            }
            .exhibition-list {
                background-color: #fff;
                border-radius: 1.5rem;
                padding: 1.7rem 1.7rem 2.3rem;
            }
        }
        .guide-list {
            padding: 3rem 1.7rem 0;
        }
        .exhibition-bn {
            padding: 3rem 1.7rem 0;
        }
    }
    



}